.courseTeacher-container {
    display: flex;
    align-items: center;
    justify-content: center;
    -webkit-display: box;
    -moz-display: box;
    -ms-display: flexbox;
    -webkit-display: flex;
    flex-wrap: wrap;
    padding: 0;
    margin: 10px auto;
 }    // background-color: #52BE7F

.courseTeacher-cards {
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 1rem;
    list-style: none;
    height: 410px;
    & .courseTeacher-card-item {
        display: flex;
        flex-direction: column;
        background-color: #FFFFFF;
        width: 100%;
        border-radius: 6px;
        box-shadow: 0 20px 40px -14px rgba(0, 0, 0, 0.25);
        overflow: hidden;
        transition: transform 0.5s;
        -webkit-transition: transform 0.5s;
        text-decoration: none;
        text-align: center;
        color: black;
        height: 100%;
        &:hover {
            // cursor: pointer
            transform: scale(1.1);
            -webkit-transform: scale(1.03);
            & .courseTeacher-card-image {
                opacity: 1; } } }
    & .courseTeacher-card-info {
        display: flex;
        flex: 1 1 auto;
        flex-direction: column;
        padding: .25rem;
        align-items: center;
        color: black;
        & .courseTeacher-card-intro {
            width: 100%;
            font-size: 13px;
            height: 30%; } }

    & .courseTeacher-card-subtitle {
        display: inline; }

    & .courseTeacher-card-title {
        font-size: 20px;
        color: #000000;
        height: 15%; }
    & .courseTeacher-card-image {
        height: 180px;
        overflow: hidden;
        background-size: cover;
        background-position: center;
        background-repeat: no-repeat;
        border-radius: 6px 6px 0px 0px;
        opacity: 0.91;
        background-image: url('https://www.petdarling.com/articulos/wp-content/uploads/2016/04/nombres-de-gatos.jpg'); } }

@media (min-width: 896px) {
    .courseTeacher-cards {
        width: 290px; } }
