.login100-form {
    width: 100%;

    &-title {
        display: block;
        font-family: Opal;
        font-size: 30px;
        color: #333333;
        line-height: 1.2;
        text-align: center; }

    &-title i {
        font-size: 60px; } }

.wrap-input100 {
    width: 100%;
    position: relative;
    border-bottom: 3px solid #adadad;
    margin-bottom: 37px; }

.input100 {
    font-family: Opal;
    font-size: 15px;
    color: #555555;
    line-height: 1.2;
    border: 0;
    display: block;
    width: 100%;
    height: 45px;
    background: transparent;
    padding: 0 5px;
    outline: none;
    border: none; }
.focus-input100 {
    position: absolute;
    display: block;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    pointer-events: none;

    &::before {
        content: "";
        display: block;
        position: absolute;
        bottom: -2px;
        left: 0;
        width: 0;
        height: 2px;

        -webkit-transition: all 0.4s;
        -o-transition: all 0.4s;
        -moz-transition: all 0.4s;
        transition: all 0.4s;

        background: #6a7dfe;
        background: -webkit-linear-gradient(to left, #21d4fd, #b721ff);
        background: -o-linear-gradient(to left, #21d4fd, #b721ff);
        background: -moz-linear-gradient(to left, #21d4fd, #b721ff);
        background: linear-gradient(to left, #21d4fd, #b721ff); }

    &::after {
        font-family: Opal;
        font-size: 15px;
        color: #999999;
        line-height: 1.2;

        content: attr(data-placeholder);
        display: block;
        width: 100%;
        position: absolute;
        top: 16px;
        left: 0px;
        padding-left: 5px;

        -webkit-transition: all 0.4s;
        -o-transition: all 0.4s;
        -moz-transition: all 0.4s;
        transition: all 0.4s; } }

.input100:focus+::after {
    top: -15px; }

.input100:focus+::before {
    width: 100%; }

.has-val.input100+::after {
    top: -15px; }

.has-val.input100+::before {
    width: 100%; }

.container-login100-form-btn {
