.CourseDetail__Container {
    display: block;
    text-align: center;
    padding: 5px 50px; }
.CourseDetail__title {
    text-align: center;
    font-size: 2.4em;
    color: #070707; }
.CourseDetail__table {
    margin: auto;
    min-width: 300px;
    max-width: 100%;
    border-collapse: collapse;
    color: #333;
    border-radius: .4em;
    overflow: hidden;
    .CourseDetail__table-tr {
        &:first-child {
            border-top: none;
            background: #428bca;
            color: #000000;
            border-top: 1px solid #ddd;
            border-bottom: 1px solid #ddd;
            background-color: #ddd; }
        &:nth-child(odd):not(:first-child) {
            background-color: rgb(199, 211, 220); } }
    .CourseDetail__table-th {
        display: none; }
    .CourseDetail__table-td {
        display: block;
        &:first-child {
            margin-top: .5em; }
        &:last-child {
            margin-bottom: .5em; }
        &:before {
            content: attr(data-th) ":";
            font-weight: bold;
            width: 120px;
            display: inline-block;
            color: #000; } }
    .CourseDetail__table-th, .CourseDetail__table-td {
        text-align: left;
        padding: .5em 1em; }
    .CourseDetail__table-tr {
        border-color: #bfbfbf; }
    .courseTeacher__button-alumno {
        background-color: #FB9600;
        min-width: 30px;
        max-width: 40px;
        border: 0;
        height: 30px;
        // display: block
        margin: .1em;
        border-radius: 5px;
        color:#fff {}
        // vertical-align: middle
        // position: relative
        z-index: 1;
        width: 100%;
        cursor: pointer;
        float: right; } }

@media screen and (max-width: 601px) {
    .CourseDetail__table .CourseDetail__table-tr:nth-child(2) {
        border-top: none; }
    .CourseDetail__Container {
            padding: 0; } }

@media screen and (min-width: 600px) {
    .CourseDetail__table {
        .CourseDetail__table-tr:hover:not(:first-child) {
            background-color: #d8e7f3; }
        .CourseDetail__table-td:before {
            display: none; }
        .CourseDetail__table-th, .CourseDetail__table-td {
            display: table-cell;
            padding: 1em; }
        .CourseDetail__table-th:first-child, .CourseDetail__table-td:first-child {
            text-align: center; } } }
