.divSketch {
    position: relative;
    width: 1200px;
    left: calc(50% - 600px);
    display: flex;
    flex-wrap: wrap;
    list-style: none;
    margin: 0;
    padding: 0;
    .canvasBoard {
        display: flex;
        width: 1000px; }
    .divCuadro {
        width: 100px;
        top: 10px;
        position: relative;
        background-color: #f0f0f0;
        border-radius: 10px;
        border: 1px solid #cccccc;
        text-align: center;
        padding: 15px 5px 5px 5px; }
    & .contColor {
        .ulcolors {
            display: flex;
            flex-wrap: wrap;
            list-style: none;
            padding: 0;
            margin-top: 0px;
            margin-bottom: 0px;
            li {
                width: 50%;
                display: flex;
                padding: 3px;
                justify-content: center;

                div {
                    width: 30px;
                    height: 30px;
                    border-radius: 50%;
                    cursor: pointer; }
                & .white {
                    background: white; }
                & .silver {
                    background: silver; }
                & .gray {
                    background: gray; }
                & .black {
                    background: black; }
                & .red {
                    background: red; }
                & .maroon {
                    background: maroon; }
                & .yellow {
                    background: yellow; }
                & .olive {
                    background: olive; }
                & .lime {
                    background: lime; }
                & .green {
                    background: green; }
                & .aqua {
                    background: aqua; }
                & .teal {
                    background: teal !important; }
                & .blue {
                    background: blue; }
                & .navy {
                    background: navy; }
                & .fuchsia {
                    background: fuchsia; }
                & .purple {
                    background: purple; } }
            & .sombreado {
                background-color: #D6D6D6;
                border-radius: 8px; } } }
    & .contSize {
        .ulSize {
            display: flex;
            flex-wrap: wrap;
            list-style: none;
            padding: 0;
            position: relative;
            margin-top: 0px;
            li {
                width: 100%;
                display: flex;
                padding: 3px;
                justify-content: center;
                div {
                    border-radius: 50%;
                    cursor: pointer;
                    background: black; }
                & .size-4 {
                    position: relative;
                    top: calc(50% - 2px);
                    width: 4px;
                    height: 4px; }
                & .size-8 {
                    position: relative;
                    top: calc(50% - 4px);
                    width: 8px;
                    height: 8px; }
                & .size-12 {
                    position: relative;
                    top: calc(50% - 6px);
                    width: 12px;
                    height: 12px; }
                & .size-16 {
                    position: relative;
                    top: calc(50% - 8px);
                    width: 16px;
                    height: 16px; }
                & .size-20 {
                    position: relative;
                    top: calc(50% - 10px);
                    width: 20px;
                    height: 20px; }
                & .size-24 {
                    position: relative;
                    top: calc(50% - 12px);
                    width: 24px;
                    height: 24px; } }
            & .sombreado {
                background-color: #D6D6D6;
                border-radius: 8px; } }
        .ulOptions {
            margin-top: 0px;
            display: flex;
            flex-wrap: wrap;
            list-style: none;
            padding: 0;
            position: relative;
            li {
                width: 100%;
                display: flex;
                padding: 3px;
                justify-content: center;
                i {
                    color: black; } }
            & .sombreado {
                background-color: #D6D6D6;
                border-radius: 8px; } } } }

