//Caja Principal
.footer-distributed {
    overflow : hidden;
    background: #1C59AA;
    color: white;
    display: flex;
    text-align: center;
    .footer-div {
        flex-grow: 1;
        display: flex;
        cursor: pointer;
        &:hover {
            background: #ffffff33; }
        img {
            margin: auto 3px auto auto; }
        span {
            margin: auto auto auto 3px; } } }

//Boton video
.btnenviarvideo {
    background-color: #52BE7F;
    color: white;
    padding: 2px 10px;
    cursor: pointer;
    font-size: 20px;
    border-radius: 5px;
    margin: -2px 8px;
    .button-zoom {
        transition: transform 0.3s ease-in-out;
        -webkit-transition: all .4s ease;
        -moz-transition: all .4s ease;
        -o-transition: all .4s ease;
        -ms-transition: all .4s ease;
        &:hover {
            -webkit-transform: scale(1.1);
            transform: scale(1.1); } } }

.btnMyM {
    background-color: #3d8f2d;
    color: white;
    padding: 2px 10px;
    cursor: pointer;
    font-size: 20px;
    border-radius: 5px;
    margin: -2px 8px;
    .button-zoom {
        -webkit-transition: all .44s eases;
        -moz-transition: all .4s ease;
        -o-transition: all .4s ease;
        -ms-transition: all .4s ease;
        &:hover {
            -webkit-transform: scale(1.25);
            -moz-transform: scale(1.25);
            -ms-transform: scale(1.25);
            -o-transform: scale(1.25);
            transform: scale(1.3); } } }


//boton
.Myni {
    background-color: #52BE7F;
