.loginTeacher {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  font-family: opal;
  background-image: url(./bg-teacher-login.jpg);
  background-color: #5356ad;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  overflow: hidden; }

.loginTeacher-table {
  display: table;
  width: 100%;
  height: 100%; }

.loginTeacher-table-cell {
  box-sizing: content-box !important;
  display: table-cell;
  vertical-align: middle;
  -moz-transition: all 0.5s;
  -o-transition: all 0.5s;
  -webkit-transition: all 0.5s;
  transition: all 0.5s; }

.loginTeacher-subtitle {
  margin: 0 auto 15px;
  text-align: center;
  font-size: 1.5em;
  font-weight: bold;
  letter-spacing: normal; }

.loginTeacher-container {
  position: relative;
  width: 600px;
  margin: 30px auto 0;
  height: 320px;
  top: 50%;
  margin-top: -160px;
  -moz-transition: all 0.5s;
  -o-transition: all 0.5s;
  -webkit-transition: all 0.5s;
  transition: all 0.5s;
  .loginTeacher-box {
    background: rgba(0, 0, 0, 0.5);
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    overflow: hidden;
    &:before {
      content: " ";
      position: absolute;
      left: 152px;
      top: 50px;
      background-color: #4f8e79c9;
      transform: rotateX(52deg) rotateY(15deg) rotateZ(-38deg);
      width: 300px;
      height: 285px;
      -moz-transition: all 0.5s;
      -o-transition: all 0.5s;
      -webkit-transition: all 0.5s;
      transition: all 0.5s; }
    &:after {
      content: " ";
      position: absolute;
      left: 152px;
      top: 50px;
      background-color: #4f8e79c9;
      transform: rotateX(52deg) rotateY(15deg) rotateZ(-38deg);
      width: 300px;
      height: 285px;
      -moz-transition: all 0.5s;
      -o-transition: all 0.5s;
      -webkit-transition: all 0.5s;
      transition: all 0.5s;
      background-color: #4f8e79c9;
      top: -10px;
      left: 80px;
      width: 320px;
      height: 180px; } }
  .loginTeacher-container-forms {
    position: relative; }
  .loginTeacher-btn {
    border: none;
    cursor: pointer;
    text-align: center;
    margin: 0 auto;
    width: 130px;
    color: #fff;
    background-color: #52be7f;
    opacity: 1;
    -moz-transition: all 0.5s;
    -o-transition: all 0.5s;
    -webkit-transition: all 0.5s;
    transition: all 0.5s;
    padding: 10px 15px;
    &:hover {
      opacity: 0.7;
      padding: 10px 15px; } }
  input {
    padding: 10px 15px;
    margin: 0 auto 15px;
    margin: 0 auto 15px;
    box-sizing: content-box;
    border: none;
    display: block;
    font-family: monospace;
    width: 220px;
    font-size: 14px;
    -moz-transition: all 0.3s;
    -o-transition: all 0.3s;
    -webkit-transition: all 0.3s;
    transition: all 0.3s; }
  .loginTeacher-container-forms .loginTeacher-container-info {
    text-align: left;
    font-size: 0;
    .loginTeacher-info-item {
      text-align: center;
      font-size: 16px;
      width: 300px;
      height: 320px;
      display: inline-block;
      vertical-align: top;
      color: #fff;
      opacity: 1;
      -moz-transition: all 0.3s;
      -o-transition: all 0.3s;
      -webkit-transition: all 0.3s;
      transition: all 0.3s;
      p {
        font-size: 32px;
        margin: 20px; }
      .loginTeacher-btn {
        background-color: transparent;
        border: 1px solid #fff; }
      .loginTeacher-table-cell {
        padding-right: 35px; }
      &:nth-child(2) .loginTeacher-table-cell {
        padding-left: 35px;
        padding-right: 0; } } }
  .loginTeacher-container-form {
    overflow: hidden;
    position: absolute;
    left: 30px;
    top: -30px;
    width: 305px;
    height: 380px;
    background-color: rgb(230, 230, 230);
    box-shadow: 0 0 15px 0 rgba(0, 0, 0, 0.2);
    -moz-transition: all 0.5s;
    -o-transition: all 0.5s;
    -webkit-transition: all 0.5s;
    transition: all 0.5s;
    border-radius: 5%;
    .loginTeacher-btn {
      position: relative;
      box-shadow: 0 0 10px 1px #52be7f;
      font-family: Opal;
      font-size: 15px; } }
  .loginTeacher-form-item {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    opacity: 1;
    -moz-transition: all 0.5s;
    -o-transition: all 0.5s;
    -webkit-transition: all 0.5s;
    transition: all 0.5s;
    &.sign-up {
      position: absolute;
      left: -100%;
      opacity: 0; } }
  &.log-in {
    .loginTeacher-box {
      &:before {
        position: absolute;
        left: 180px;
        top: 62px;
        height: 265px; }
      &:after {
        top: 22px;
        left: 192px;
        width: 324px;
        height: 220px; } }
    .loginTeacher-container-form {
      left: 265px;
      .loginTeacher-form-item {
        &.sign-up {
          left: 0;
          opacity: 1; }
        &.log-in {
          left: -100%;
          opacity: 0; } } } }
  &.active {
    width: 260px;
    height: 140px;
    margin-top: -70px;
    .loginTeacher-container-form {
      left: 30px;
      width: 200px;
      height: 200px; }
    input, .loginTeacher-btn, .loginTeacher-info-item {
      display: none;
      opacity: 0;
      padding: 0px;
      margin: 0 auto;
      height: 0; }
    .loginTeacher-form-item {
      height: 100%; }
    .loginTeacher-container-forms .loginTeacher-container-info .loginTeacher-info-item {
      height: 0%;
      opacity: 0; } } }

//  Boton logeo con Google

.loginTeacher-button-google {
  float: left;
  font-family: opal;
  color: #ffffff !important;
  background: #4285f4;
  min-width: 90%;
  height: 40px;
  margin: 10px 0 0 13px;
  text-align: center;
  user-select: none;
  font-size: 15px;
  font-weight: 500;
  line-height: 40px;
  text-decoration: none;
  letter-spacing: 0.21px;
  border: #4285f4 1px solid;
  border-radius: 100px;
  box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.25);
  transition: background-color 0.18s, border-color 0.18s, box-shadow 0.18s;
  &:hover {
    box-shadow: 0px 0px 15px 0px rgba(66, 133, 244, 0.75); } }

.loginTeacher-google-icon {
  float: left;
  border-radius: 100px;
  background: #ffffff;
  width: 38px;
  padding: 11px;
  -webkit-transition: -webkit-transform 0.5s ease-in-out;
  transition: transform 0.5s ease-in-out; }

.loginTeacher-button-google:hover .loginTeacher-google-icon {
  -webkit-transform: rotate(360deg);
  transform: rotate(360deg); }

//  linea intermedia entre login normal y login con google

.linea {
  margin: 0 auto 15px;
  width: 40px;
  border-top: 1px solid #999;
  position: relative;
  top: 13px;
  float: left; }

.leyenda {
  margin-top: 5px;
  font-size: 12px;
  float: left; }

//  loading

.login-loading {
  margin-left: 45%;
  margin-top: 45px; }

.loginTeacher-login-register {
  display: none; }

.loginTeacher-message {
  color: white;
  text-align: center;
  background: #e91e63;
  height: 39px;
  align-content: center;
  padding: 10px;
  font-size: 15px; }

.loginTeacher-message-succefull {
  color: white;
  text-align: center;
  background: #52be7f;
  height: 39px;
  align-content: center;
  padding: 10px;
  font-size: 15px; }

//  Media queries a partir de 600 px

@media (max-width: 600px) {
  .loginTeacher-login-register {
    display: block;
    position: absolute;
    width: 100%;
    left: 33%;
    bottom: 10px;
    margin: 10px auto 0 !important;
    color: #4285f4 !important; }
  .loginTeacher-message {
    width: 305px;
    margin-top: 70px;
    margin-left: 30px;
    height: 50px; }
  .loginTeacher-container {
    .loginTeacher-container-form {
      top: -40px;
      height: 420px; }
    &.log-in .loginTeacher-container-form {
      left: 30px; } } }
.loginTeacherHeader {
  height: 51px;
  padding: 13px 19px 0;
  max-width: 255px;
  .logoAcademy {
    height: 51px;
    margin: 15px;
    background-image: url('../../img/LogoBlancoNegro.svg');
    background-repeat: no-repeat;
    &:hover {
      background-image: url('../../img/LogoColor.svg');
      background-repeat: no-repeat; } } }
.resetPassword {
  margin-top: 1.7rem;
  font-size: 15px;
  float: left;
  width: 100%;
  text-align: center; }
// estilos para ver contrasea


.loginTeacher-table-cell .input-group {
  width: 100%;
  padding: 10px 28px 20px 28px;
  display: table;
  position: relative;
  line-height: 1.75rem;
  border-radius: 2px;
  border-spacing: 0;
  & .form-control, .input-group-addon {
    display: table-cell;
    white-space: nowrap;
    vertical-align: top; }
  & .input-group-addon {
    width: 10%;
    padding: 5px 0 0 0; }
  & .form-control {
    width: 84%;
    margin: 0  !important;
    padding: 10px 15px  !important; } }

