    >.table-responsive {
        >.table-bordered {
            >tbody {
                >tr {
                    >td:last-child,
                    >th:last-child, {
                        border-right: 0; } } }
            >tfoot {
                >tr {
                    >td:last-child,
                    >th:last-child, {
                        border-right: 0; } } }
            >thead {
                >tr {
                    >td:last-child,
                    >th:last-child {
                        border-right: 0; } } } } } }


.panel {
    >.table-responsive {}
    margin-bottom: 0;
    border: 0; }
